<template>
	<v-toolbar color="primary" class="mainTopbar" height="80" dark>
    <v-layout ma-0 pa-0 align-center justify-center fill-height>
      <v-flex xs4 sm2 md2 lg3>
        <v-toolbar-title>
          <v-btn flat to="/List">
            <img src="@/assets/pptplus_logo_w.svg" width="90">
          </v-btn>
        </v-toolbar-title>
      </v-flex>
      <v-spacer></v-spacer>
      <v-flex xs6 sm12 md10 lg8>
        <v-layout class="tool">
          <v-flex xs12 md2 pl-2 pr-2>
            <v-layout align-center justify-center>
              <v-flex>
                <v-btn flat icon v-on:click="Back()">
                  <v-icon>navigate_before</v-icon>
                </v-btn>
              </v-flex>
              <v-flex>
                <v-select :items="items" v-model="picked1" :v-on:input="PickedChange()" flat></v-select>
              </v-flex>
              <v-flex>
                <v-btn flat icon v-on:click="Next()">
                  <v-icon>navigate_next</v-icon>
                </v-btn>
              </v-flex>
              <v-flex>
                <div class="toolPartLine"></div>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs2 class="display">
            <v-layout align-center justify-center>
              <v-flex xs6>
                <span class="toolText">SLIDE</span>
              </v-flex>
              <v-flex xs6>
                <v-switch v-model="slideVisible" color="secondary"></v-switch>
              </v-flex>
              <v-flex>
                <div class="toolPartLine"></div>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs2 pl-2 class="display">
            <v-layout align-center justify-center>
              <v-flex xs6>
                <span class="toolText">DATA</span>
              </v-flex>
              <v-flex xs6>
                <v-layout align-center justify-center>
                  <v-flex>
                    <v-switch v-model="data" color="secondary"></v-switch>
                  </v-flex>
                </v-layout>
              </v-flex>
              <v-flex>
                <div class="toolPartLine"></div>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs1 class="display">
            <v-layout fill-height align-center justify-center>
              <v-flex xs12>
                <v-btn flat icon v-on:click="FullScreen()">
                  <v-icon>zoom_out_map</v-icon>
                </v-btn>
              </v-flex>
              <v-flex>
                <div class="toolPartLine"></div>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs3 class="display">
            <v-layout fill-height align-center justify-center>
              <v-flex xs2>
                <v-btn flat icon v-on:click="PlaySlide()">
                  <v-icon :class="playColor">play_arrow</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs2>
                <v-btn flat icon v-on:click="Stop()">
                  <v-icon :class="stopColor">pause</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs6 class="secondInput">
                <v-text-field class="secondInputText" label="slide show time" v-model="showslidetime" solo>
                </v-text-field>
              </v-flex>
              <v-spacer></v-spacer>
            </v-layout>
          </v-flex>
          <v-flex xs1 class="display">
            <v-layout fill-height align-center justify-center>
              <v-flex>
                <div class="toolPartLine"></div>
              </v-flex>
              <v-flex xs12>
                <v-btn flat icon v-on:click="iocatcherPopup = true">
                  <v-icon>search</v-icon>
                </v-btn>
              </v-flex>
              <v-flex>
                <v-dialog v-model="iocatcherPopup" persistent max-width="1020px" max-height="540px">
                  <v-card>
                    <v-layout pt-3>
                      <v-card-title class="popupTitle">
                        Setting
                      </v-card-title>
                      <v-spacer></v-spacer>
                      <v-card-actions class="exitBtn">
                        <v-btn icon @click.stop="iocatcherPopup = false">
                          <v-icon color="primary">clear</v-icon>
                        </v-btn>
                      </v-card-actions>
                    </v-layout>
                    <v-divider></v-divider>
                    <v-layout justify-center align-center row wrap pa-2>
                      <iframe class="iocatcher" src="https://test.daialab.co.kr:1900/search/"></iframe>
                    </v-layout>
                  </v-card>
                </v-dialog>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs1 mr-1 class="display">
            <v-layout fill-height align-center justify-center>
              <v-flex>
                <div class="toolPartLine"></div>
              </v-flex>
              <v-flex xs12>
                <v-layout align-center justify-center>
                  <v-btn flat icon v-on:bind="topbarVisible = false" v-on:click="TopbarView()">
                    <v-icon>clear</v-icon>
                  </v-btn>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-spacer></v-spacer>
      <v-flex xs4 sm2 md2 lg3>
        <v-btn icon to="/List">
          <v-icon>exit_to_app</v-icon>
        </v-btn>
      </v-flex>
    </v-layout>
  </v-toolbar>
</template>

<script>
import $ from "jquery";
export default {
  data: () => ({
    picked1: null,
    showslidetime: 1000,
    slide: null,
    data: true,
    slideVisible: false,
    playColor: 'stop',
    stopColor: 'play',
    iocatcherPopup: false,
    topbarVisible: null
  }),
  props: ['picked', 'items'],
  created() {
    this.$eventBus.$on('pickReceive', (value) => {
      this.picked1 = value
    });
    this.$eventBus.$on('Subscribe_On', (data) => {
      this.data = data
    });
    this.$eventBus.$on('Unsubscribe_On', (data) => {
      this.data = data
    });
  },
  mounted() {
    this.picked1 = this.picked;
  },
  methods: {
    PickedChange() {
      this.$eventBus.$emit('pick', this.picked1);
    },
    TopbarView() {
      this.$eventBus.$emit('topbar', this.topbarVisible);
    },
    Back() {
      if (this.picked1 > 0) {
        this.picked1 += -1;
      } else {
        this.picked1 = 0;
        clearInterval(this.slide);
      }
    },
    BackSlide() {
      this.slide = setInterval(this.Back, this.showslidetime);
    },
    Next() {
      var i = this.items.length;
      if (this.picked1 < i - 1) {
        this.picked1 += 1;
      } else {
        this.picked1 = i - 1;
        this.Stop();
      }
    },
    Play() {
      const active = parseInt(this.picked1);
      this.picked1 = (active < this.items.length - 1 ? active + 1 : 0);
    },
    PlaySlide() {
      this.stopColor = 'stop';
      this.playColor = 'play';
      this.slide = setInterval(this.Play, this.showslidetime);
    },
    Stop() {
      this.stopColor = 'play';
      this.playColor = 'stop';
      clearInterval(this.slide);
    },
    FullScreen() {
      var docV = document.documentElement;
      if (docV.requestFullscreen)
        docV.requestFullscreen();
      else if (docV.webkitRequestFullscreen) // Chrome, Safari (webkit)
        docV.webkitRequestFullscreen();
      else if (docV.mozRequestFullScreen) // Firefox
        docV.mozRequestFullScreen();
      else if (docV.msRequestFullscreen) // IE or Edge
        docV.msRequestFullscreen();
    },
    iocatcherOpen() {
      // window popup open
      window.open('https://test.daialab.co.kr:1900/search/', '_blank', 'width=1020, height=440, location=0, scrollbars=yes, top=100, left=100');
    }
  },
  watch: {
    data(value) {
      if (value === true) {
        this.$eventBus.$emit('Subscribe', this.data);
      } else {
        this.$eventBus.$emit('Unsubscribe', this.data);
      }
    },
    slideVisible(value) {
      this.$eventBus.$emit('slide', this.slideVisible);
    }
  }
}
</script>
