<script>
import {mapGetters} from 'vuex';

export default{
	data: ()=>({
    isSmall: false
  }),
  computed: {
    ...mapGetters(['isLogin','getDrawerDisplay'])
  },
  methods: {
    isSmallIcon(){
      if(this.getDrawerDisplay === true && this.isLogin === true){
        this.isSmall = true;
      }else{
        this.isSmall = false;
      }
    }
  },
  updated() {
    this.$nextTick(function () {
      this.isSmallIcon();
    });
  }
}
</script>

