 </template>

<script>
import * as Msal from 'msal';
export default {
	data: ()=>({
  }),
  created() {
    // microsoft 인증 msal키 생성
    var myMSALObj = new Msal.UserAgentApplication(this.$store.state.msalConfig);
    myMSALObj.handleRedirectCallback(this.authRedirectCallBack);
    this.$store.state.myMSALObj = myMSALObj;
  },
  mounted() {
  },
  methods: {
    authRedirectCallBack(error, response) {
      if (error) {
        console.log(error);
      } else {
        if (response.tokenType === "access_token") {
          console.log("token type is:" + response.tokenType);
        } else {
          console.log("token type is:" + response.tokenType);
        }
      }
    }
  }
}
</script>

