<template>
  <v-app>
    <v-layout column>
      <v-flex xs2 class="Topbar" v-if="topbarShow">
        <core-TopbarMain :picked="picked" :items="items"/>
      </v-flex>
      <v-flex xs10 class="main_Bg">
        <v-layout column wrap>
          <template v-if="slideVisible === true">
            <v-flex xs2>
              <v-layout row pt-3>
                <v-flex>
                  <b-form-group>
                    <b-form-radio-group
                      class="slide"
                      buttons
                      button-variant="outline-danger"
                      size="lg"
                      v-model="picked"
                      :options="items"
                    />
                  </b-form-group>
                </v-flex>
              </v-layout>
            </v-flex>
          </template>
          <div class="topbar_show" v-if="!topbarShow">
            <v-layout justify-end>
              <v-btn icon v-on:click="topbarShow = true">
                <v-icon class="topbar_show_btn">arrow_downward</v-icon>
              </v-btn>
            </v-layout>
          </div>
          <v-flex xs10 class="canvas">
            <div id="container" v-on:click.left="Next()" v-on:click.middle="Back()">
            </div>
            <div id="chart"></div>
            <template v-for="(table, index) in tables">
              <div :key="index" class="tableScale">
                <contents-Table
                  :columns="table.columns"
                  :titleRows="table.titleRows"
                  :tableData="table.tableData"
                  :height="table.height"
                  :width="table.style"
                  :evenBgColor="table.evenBgColor"
                  :rowHoverColor="table.rowHoverColor"
                  :rowClickColor="table.rowClickColor"
                  v-bind:class="table.name"
                />
              </div>
            </template>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-app>
</template>

<script>
import c3 from 'c3';
import $ from "jquery";
import AllDataBinding from '../js/AllDataBinding.js';

export default {
  data() {
    return {
      picked: 0,
      items: [],
      filterItem: [],
      realData: [],
      stage: [],
      layer: [],
      subscribeData: [],
      slideVisible: null,
      charts: [],
      chartData: [],
      tables: [],
      tableData: [],
      scale: null,
      topbarShow: true
    };
  },
  created() {
    this.DB = new AllDataBinding();
    this.$eventBus.$on('pick', (value)=>{ this.picked = value });
    this.$eventBus.$on('Subscribe', this.PickedPageId);
    this.$eventBus.$on('Unsubscribe', this.Unsubscribe);
    this.$eventBus.$on('slide', (value)=>{ this.slideVisible = value });
    this.$eventBus.$on('topbar', (value)=>{ this.topbarShow = value });
  },
  mounted() {
    try{
      if(this.$store.state.accessToken === null){
        this.$router.push('/');
        this.Unsubscribe();
      }
      this.items = this.$store.state.downloadData.children;
    }catch(err){
      return;
    }
  },
  methods: {
    initSetData(item){
      if(item !== null){
        var setItem = JSON.parse(item);
        this.Binding(setItem);
      }
    },
    PickedPageId(){
      try{
        var bindingDataSet = this.items[this.picked].children;
        var bindingData = bindingDataSet.filter(this.FindDataName);
      }catch(err){
        return;
        throw(err);
      }
    },
    FindDataName(item){
      var bindingIdlength = item.children;
      console.log("BindingIdlength",bindingIdlength);
      for(let i = 0; i < bindingIdlength.length; i++){
        var pageName = item.children[i].attrs.name;
        if(pageName){
          var subDataFilter = this.subscribeData.filter(function (item) {
            return item == pageName
          });
          // server에서 subscript되고 있는 data를 두번 수신하지 않기 위한 코드
          if(subDataFilter.length === 0){
            // SetData call
            this.$socket.emit(this.$socketAPI.SetData,pageName,this.initSetData);
            // Subscribe
            this.subscribeData.push(pageName);
            this.$socket.emit(this.$socketAPI.Subscribe,pageName);
            this.Subscribe(pageName);
          }
        }else{
          console.log("pageName undefined");
        }
      }
    },
    Subscribe(PageName){
      this.$socket.on(this.$socketAPI.Message+PageName, (d) =>
      {
        this.realData = JSON.parse(d);
        console.log("subData_name",this.realData.name);
        console.log("subData_Data",this.realData);
      });
      this.dataVisible = true;
      this.$eventBus.$emit('Subscribe_On', this.dataVisible);
    },
    Unsubscribe(){
      this.$socket.emit(this.$socketAPI.Unsubscribe,this.subscribeData);
      this.subscribeData = [];
      this.dataVisible = false;
      this.$eventBus.$emit('Unsubscribe_On', this.dataVisible);
    },
    KonvaCreate(){
      try{
        this.NameFilter();

        var stage = Konva.Node.create(this.filterItem, 'container');
        // Stage등록
        this.stage = stage;

        this.layer = new Konva.Layer();
        
        this.NewTextRect();
        this.ImageConnect(stage);
        this.Binding();
      }catch(error){
        return;
        throw(error);         
      }
    },
    MonitorSize(){
      // 현재 모니터 해상도 구하기
      var monitorWidth = screen.width;
      var monitorHeight = screen.height;
      // alert("현재 접속하신 기기 화면의 너비는 " + w + "px이며, 높이는" + h + "px입니다.");

      // 가져온 파일의 해상도 가져오기
      var contents = this.items[this.picked];
      var pageWidth = contents.attrs.width;
      var pageHeight = contents.attrs.height;      
      
      // 가져온 파일의 해상도와 모니터 해상도가 안맞을 경우
      if( monitorWidth !== pageWidth && monitorHeight !== pageHeight ){
        // scale비율 구하기
        var scaleW = monitorWidth / pageWidth;
        var scaleH = monitorHeight / pageHeight;

        // 모니터 해상도에 따라 비율 적용
        if( scaleW < scaleH){
          this.scale = scaleW;
        }else{
          this.scale = scaleH;
        }
        
        // 파일의 해상도 모니터 해상도에 맞추어 다시조정하기
        var itemsPage = this.items[this.picked].attrs;

        itemsPage.width = pageWidth * this.scale;
        itemsPage.height = pageHeight * this.scale;
      }
    },
    Scale(){
      // konva scale 조정
      this.stage.scaleX(this.scale);
      this.stage.scaleY(this.scale);
      this.stage.draw();
      // Chart scale 조정
      $(`#chart`).css({
        'transform': 'scale('+this.scale+','+this.scale+')'
      });      
      // table scale 조정 NewTableCSS에서 조정
    },
    NameFilter(){
      var contents = this.items[this.picked];      
      var masterData = contents.children[0].children;
      // Chart Table Delete
      this.ChartRemove();
      this.TableRemove();

      // Chart 감지 Chart Data 저장
      var chartDataFilter = masterData.filter(function (item) {
        return item.className == "Chart"
      });
      this.chartData = chartDataFilter;

      // Table 감지 Table Data 저장
      var tableDataFilter = masterData.filter(function (item) {
        return item.className == "Table"
      });
      this.tableData = tableDataFilter;

      // Chart나 Table의 데이터가 없는 경우
      if(chartDataFilter.length === 0 && tableDataFilter.length === 0){
        console.log("chart off")
        this.filterItem = contents;
        // 기존 Chart Table 데이터 제거
        if(this.charts.length !== 0){
          // this.charts.destroy(); //차트 삭제
          this.ChartRemove();
          this.TableRemove();
        }
      }
      
      // Chart나 Table의 데이터가 있는 경우
      if( chartDataFilter.length !== 0 || tableDataFilter.length !== 0 ){
        
        // Contents Data 복사 > 원본데이터 변경 방지
        var copyContents = $.extend( true, {}, contents);

        // Chart Data가 있는 경우
        if(chartDataFilter.length !== 0){
          var chartMasterData = copyContents.children[0].children;
          var chartlength = this.chartData.length;
          // 감지된 Chart 만큼 반복
          for(let i = 0; i < chartlength; i++) {
            // Chart Data index찾기
            var chartDataFind = chartMasterData.findIndex(function (item) {
              return item.className == "Chart"
            });
            // Chart 데이터 삭제 > Chart는 konva에서 인식 안됨
            if (chartDataFind > -1) chartMasterData.splice(chartDataFind, 1);
          }
          this.NewChart();
        }
        // Chart Data가 없는 경우
        if(chartDataFilter.length === 0){
          this.ChartRemove();
        }
        // Table Data가 있는 경우
        if(tableDataFilter.length !== 0){
          var tableMasterData = copyContents.children[0].children;
          var tablelength = this.tableData.length;
          // 감지된 Table 만큼 반복
          for(let i = 0; i < tablelength; i++) {
            // Table Data index찾기
            var tableDataFind = tableMasterData.findIndex(function (item) {
              return item.className == "Table"
            });
            // Table 데이터 삭제 > Table은 konva에서 인식 안됨
            if (tableDataFind > -1) tableMasterData.splice(tableDataFind, 1);
          }
          this.NewTable();
        }
        // Table Data가 없는 경우
        if(tableDataFilter.length === 0){
          this.TableRemove();
        }
        // FilterItem으로 보내기
        this.filterItem = copyContents;
      }
    },
    NewChart(){
      // 기존 Chart 데이터 삭제
      this.charts = [];
      var chartlength = this.chartData.length;
      try{
        for(let i = 0; i < chartlength; i++) {
          var chartData = this.chartData[i].attrs

          // Name 생성
          var chartName = chartData.name;

          // chartName 앞에 c3_ 추가 = chartName 맨 앞에 숫자가 오게되면 c3차트를 그릴 수 없음
          // Html 생성
          var $createChart = $('<div id='+chartData.id+' class='+'c3_'+chartName+'></div>');
          var createChart = document.createElement('div');
          $createChart.appendTo($('#chart'));
          
          // CSS 변경
          $(`.c3_`+chartName).css({
            'max-width': chartData.width,
            'margin-left': chartData.x+'px',
            'margin-top': chartData.y+'px',
            'z-index': chartData.setZIndex
          });
          
          // // Data name 찾기
          // var dataNameFind = [];
          // for(let n = 0; chartData.data.length > n; n++){            
          //   var dataNameFilter = Object(chartData.data[n])[0];
          //   dataNameFind.push(dataNameFilter);
          // }
          // console.log("dataNameFind",dataNameFind);

          var chartProperties = {
            bindto: `.c3_`+chartName, // bind id 입력
            size: {
              width: chartData.width,
              height: chartData.height
            },
            data: {
              x: 'x',
              columns: chartData.data,
              type: chartData.chartType,
              types: chartData.chartTypes,
              axes: chartData.axes,
              groups: chartData.groups
              // labels: true
            },
            pie: {
              label: {
                  format: function (value, ratio, id) {
                    var ratioPer = ratio*100;
                    var ratioResult = ratioPer.toPrecision(3);
                      return value+` /\n`+ratioResult+`(%)`;
                  }
              }
            },
            donut: {
              // title 입력시 차트 중심에 입력가능
              title: '',
              label: {
                format: function (value, ratio) {
                  var ratioPer = ratio*100;
                  var ratioResult = ratioPer.toPrecision(3);
                  var dataPush = value+` /\n`+ratioResult+`(%)`;
                  return dataPush;
                }
              }
            },
            color: {
              pattern: chartData.color
            },
            axis:{
              x:{
                type: 'category',
                // tick name rotate option
                tick: chartData.axisTick,
                height: chartData.legendHeight
              },
              y2:{
                show: chartData.showY2
              }
            },
            grid:{
              x: {
                show: chartData.gridX,
                lines: chartData.gridXLines
              },
              y: {
                show: chartData.gridY,
                lines: chartData.gridYLines
              }
            },
            legend: chartData.legend,
            zoom:{
              enabled: true
            }
          }
          // Chart 생성
          var newChart = c3.generate(chartProperties);

          // this.charts에 Name값 추가
          newChart.name = chartName;

          // Chart Data Push
          this.charts.push(newChart);
        }
      } catch(error){
        return;
      }
    },
    ChartCSS(){
      // chart legend font size resizing
      if(this.chartData.length > 0){
        if(this.chartData[0].attrs.legendFontSize !== undefined || this.chartData[0].attrs.legendFontSize !== null){
          // legend title css 변경
          // $('.c3-legend-item').css('font-size','20px');
          // Chart legend css 변경
          $('.c3 svg').css({
            'font-size':this.chartData[0].attrs.legendFontSize+'px'
          });
        }
      }
    },
    NewTable(){
      // 기존 Table데이터 삭제
      this.tables = [];
      var tablelength = this.tableData.length;
      try{
        for(let i = 0; i < tablelength; i++) {
          var tableData = this.tableData[i].attrs;
          tableData.style = 'width:'+tableData.width+'px';
          // Table Data Push
          this.tables.push(tableData);
        }
      } catch(error){
        return;
      }
    },
    NewTableCSS(){
      var tablelength = this.tableData.length;
      try{
        for(let i = 0; i < tablelength; i++) {
          var tableData = this.tableData[i].attrs;          
          // CSS 변경
          $(`.`+tableData.name).css({
            'width': '100%',
            'max-height': '100%',
            'margin-left': tableData.x+'px', // scale변환으로 인해 스케일 값을 곱해준다.
            'margin-top': tableData.y+'px',
            'z-index': tableData.setZIndex,
            'position': 'absolute',
          });
        }
        // Table scale적용
        $(`.tableScale`).css({
          'transform': 'scale('+this.scale+','+this.scale+')'
        });
      } catch(error){
        return;
      }
    },
    ChartRemove(){
      // id chart안의 내용을 지워줌
      $('#chart').empty();
      document.getElementById('chart').innerHTML = '';
    },
    TableRemove(){
      this.tables = [];
    },
    NewTextRect(){
      var textFilter = [];
      var rectDatafilter = [];
      
      textFilter = this.stage.children[0].children;
      var rectData = textFilter.filter(function (item) {
        return item.className == "Rect"
      });
      rectDatafilter = rectData;
      // filter된 데이터를 변수에 담아주지 않으면 하위 filter에서 text를 찾지 못함
      var rectDatalength = rectDatafilter.length;
      for(let i = 0; i < rectDatalength; i++) {
        var rectId = rectData[i].index;
        // console.log(rectData[i].attrs);
        var complexText = new Konva.Text({
          x: rectData[i].attrs.x,
          y: rectData[i].attrs.y,
          width: rectData[i].attrs.width,
          height: rectData[i].attrs.height,
          text: rectData[i].attrs.text,
          fontSize: rectData[i].attrs.fontSize,
          fontFamily: rectData[i].attrs.fontFamily,
          align: rectData[i].attrs.align,
          verticalAlign:'middle',
          fill: 'white',
          cornerRadius: 0,
          name: rectData[i].attrs.name+'T',
          setZIndex: rectData[i].attrs.fontFamily + 1
        });
        this.layer.add(complexText);
        this.stage.add(this.layer);
        this.DB.Rect();
      }
    },
    ImageConnect(stage){
      // set images
      var img = stage.get('Image');
      for(var n = 0; n < img.length; n++) {
        var imageObj = new Image();
        // scr = url을 가지고 있는 경우
        // shapeImage = base64 이미지 코드인 경우
        if(this.stage.get('Image')[n].attrs.src !== null){
          var src = this.stage.get('Image')[n].attrs.src;
        }if(this.stage.get('Image')[n].attrs.src == null){
          var base64Image = this.stage.get('Image')[n].attrs.shapeImage;
          var src = 'data:image/jpeg;base64,'+base64Image;
        }
        imageObj.src = src;
        imageObj.temp = n;
        imageObj.onload = function() {
          stage.get('Image')[this.temp].image(this);
          stage.draw();
        };
      };
    },
    Binding(data){
      this.DB.Image(data,this.stage);
      this.DB.Chart(data,this.charts);
      this.DB.Table(data,this.tables);
      this.DB.Circle(data,this.stage);
      this.DB.RegularPolygon(data,this.stage);
      this.DB.Rect(data,this.stage);
      this.DB.Line(data,this.stage);
      this.DB.Text(data,this.stage);
    },
    Next(){
      // 첫페이지로 다시 돌아감
      const active = parseInt(this.picked);
      this.picked = ( active < this.items.length-1 ? active + 1 : 0 );
    },
    Back(){
      if(this.picked > 0){
          this.picked += -1;
      }else{
          this.picked = 0;
      }
    },
    ScreenCheck(){
      $( window ).resize(function() {
        // 창의 크기와 모니터의 크기를 비교하여 fullscreen인지 알아낸다.
        var windowHeight = $( window ).height();
        var monitorHeight = screen.height;

        if(windowHeight !== monitorHeight){
          // v-if값으로 값이 적용되지 않아 css로 보이지 않게 처리
          $(`.Topbar`).css({
            'display': 'block'
          });
        }if(windowHeight === monitorHeight){
           $(`.Topbar`).css({
            'display': 'none'
          }); 
        }
      });
    },
    MouseControl(){
      // 마우스 휠 이벤트 Test
      $("html, body").on('mousewheel DOMMouseScroll', function(e) {
        var E = e.originalEvent;
        var delta = 0;
        
        if (E.detail) {
          delta = E.detail * -40;
          // console.log("delta",delta);
        }else{
          delta = E.wheelDelta;
          // console.log("delta",delta);
        };
      });
    }
  },
  watch:{
    picked(index){
      if(index !== null){
        this.Unsubscribe();
        this.PickedPageId();
        this.MonitorSize();
        this.KonvaCreate();
        this.$eventBus.$emit('pickReceive', index);
      }
    },
    realData(data){
      this.Binding(data);
    }
  },
  updated() {
    this.$nextTick(function () {
      this.Scale();
      this.ChartCSS();
      this.NewTableCSS();
      this.ScreenCheck();
    });
  },
  beforeDestroy(){
    try{
      this.Unsubscribe();
      this.$socket.disconnect();
      if(this.$store.state.accessToken === null){
        this.$router.push('/').catch(err => err);
        this.Unsubscribe();
      }
      this.items = this.$store.state.downloadData.children;
    }catch(err){
      return;
    }
  }
};
</script>

