<template>
  <v-navigation-drawer
    class="pageNaviBg"    
    right
    clipped
    fixed
    app
    width="220"
  >
    <v-list
      v-scroll-spy-active="{class: 'customActive'}"
      v-scroll-spy-link
    >
      <v-list-tile
        v-for="item in items"
        :key="item.title"
        v-on:click="fnMove(item.href)"
      >
        <v-list-tile-content>
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import $ from "jquery";

export default {
  props: ['items', 'section'],
  created() {
  },
  data: () => ({
  }),
  methods: {
    fnMove(href){
      var item = href;
      var seq = item.substr(item.length - 1);
      var offset = $("#document" + seq).offset();
      $('html, body').animate({scrollTop : offset.top-40}, 500);
    }
  },
  updated() {
    this.$nextTick(() => {
    });
  }
}
</script>

<style>
.pageNaviBg{
  padding-top: 3rem;
  background-color: #F3F3F3 !important;
}
.customActive {
  color: #b7472a;
  border-left: 3px solid #b7472a;
  transition: all 0.5s;
}
</style>