<template>
  <v-container class="list_bg" fluid fill-height ma-0>
    <v-layout row pt-3 align-start justify-center>
      <v-flex xs11 sm10 lg8>
        <v-card>
          <v-toolbar flat large class="menuToolbar" color="bgcolor">
            <v-layout row wrap>
              <v-flex xs12 sm6 md8 lg9>
                <v-layout align-center>
                  <v-icon large color="primary" class="ml-3">person</v-icon>
                  <v-toolbar-title>{{userName}} _OneDrive File</v-toolbar-title>
                </v-layout>
              </v-flex>
              <v-flex xs12 sm6 md4 lg3>
                <v-layout v-if="fileName" class="fileName" align-center mr-4>
                  <v-icon class="mr-3" color="primary">layers</v-icon>
                  <span>{{fileName}}</span>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-toolbar>
          <v-layout class="onedrive_menu">
            <v-flex>
              <v-tabs icon fixed-tabs v-model="picked" slider-color="primary">
                <v-tab v-for="(itemGet, index) in getData" :key="index">
                  <v-icon color="primary">{{ itemGet.icon }}</v-icon>
                  <span class="onedrive_text">
                    {{ itemGet.name }}
                  </span>
                </v-tab>
              </v-tabs>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs12>
              <!-- .daia일 경우 list -->
              <v-list>
                <v-radio-group>
                  <v-list-tile v-for="item in listData" :key="item.name" avatar class="border_bottom" @click="CheckData(item)">
                    <v-btn color="startbtn" :disabled="item.startbtn" class="start_btn" to="Main">Start</v-btn>
                    <v-list-tile-avatar>
                      <v-icon class="list_icon" :color="item.color">{{item.icon}}</v-icon>
                    </v-list-tile-avatar>
                    <v-layout fill-height>
                      <v-flex>
                        <v-list-tile-content>
                          <v-list-tile-title v-html="item.name"></v-list-tile-title>
                        </v-list-tile-content>
                      </v-flex>
                      <v-spacer></v-spacer>
                      <v-flex>
                        <v-layout fill-height align-center>
                          <v-list-tile-sub-title class="update_date" v-html="item.lastModifiedDateTime">
                          </v-list-tile-sub-title>
                        </v-layout>
                      </v-flex>
                    </v-layout>
                  </v-list-tile>
                </v-radio-group>
              </v-list>
              <!-- .daia가 아닌 list -->
              <v-list>
                <v-list-tile v-for="item in listDatafile" :key="item.name" avatar class="border_bottom">
                  <v-layout fill-height align-center ml-5>
                    <v-list-tile-avatar>
                      <v-icon :color="item.color">{{item.icon}}</v-icon>
                    </v-list-tile-avatar>
                    <v-layout fill-height>
                      <v-flex>
                        <v-list-tile-content>
                          <v-list-tile-title v-html="item.name"></v-list-tile-title>
                        </v-list-tile-content>
                      </v-flex>
                      <v-spacer></v-spacer>
                      <v-flex>
                        <v-layout fill-height align-center>
                          <v-list-tile-sub-title class="update_date" v-html="item.lastModifiedDateTime">
                          </v-list-tile-sub-title>
                        </v-layout>
                      </v-flex>
                    </v-layout>
                  </v-layout>
                </v-list-tile>
              </v-list>
              <v-dialog v-model="dialog" persistent width="300">
                <v-card color="primary" dark>
                  <v-card-text>
                    선택한 파일을 다운 받는 중입니다.
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                  </v-card-text>
                </v-card>
              </v-dialog>
            </v-flex>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Vue from "vue";
import oneDriveAPI from "onedrive-api";
import * as Promise from 'bluebird';
import $ from "jquery";
import socketio from "socket.io-client";

export default{
	data: ()=>({
		items: null,
    picked: 0,
    listData: [],
    listDatafile: [],
    getData: [],
    userName: null,
    startbtn: true,
    downloadData: {},
    loader: null,
    loading: false,
    dialog: false,
    fileName: ''
  }),
  computed: {
  },
  beforeCreate() {
    try{
      if(this.$store.state.accessToken === null){
        this.$router.push('/'); //accessToken이 없다면 login화면으로 보내기
      }
    }catch(err){
      throw(err);
    }
  },
  created() {
    try{
      this.GetOneDrivedata();
    }catch(err){
      console.log(err);
    }
  },
	mounted(){
    this.SocketConnect();
    this.Unsubscribe();
	},
	methods:{
    GetPicked(){
      this.IconFolder();
      // this.getData에서 ppt+ 이름을 찾아 this.picked 넣어준다.
      var getPptName = this.getData.findIndex(function(item){
        return item.name == 'ppt+'
      });
      this.picked = getPptName;
    },
    IconFolder(){
      // icon folder push
      for(let i = 0; i < this.getData.length; i++){
        this.getData[i].icon = 'folder';
      }
    },
    StartBtnReset(){
      // startbtn disable states reset
      for(let i = 0; i < this.listData.length; i++){
        this.listData[i].startbtn = true;
      }
    },
    SocketConnect(){
      Vue.prototype.$socket = socketio.connect(this.$setting.EXPRESS_SERVER_HOST + ':' + this.$setting.EXPRESS_SERVER_PORT);
    },
		GetOneDrivedata(){
      oneDriveAPI.items.getMetadata({
        accessToken: this.$store.state.accessToken,
        itemId: 'root/children'
      }).then((item) => {
        this.getData = item.value;
        var data = item.value[0];
        this.GetUserName(data);
        this.GetPicked();
        console.log("getData",this.getData);
      })
    },
    GetUserName(data){
      var user = data.createdBy.user.displayName;
      var arr = user.split("");
      this.userName = arr[0]+arr[2]+arr[3];
    },
    GetList(listID){
      // list data 요청
      oneDriveAPI.items.listChildren({
        accessToken: this.$store.state.accessToken,
        itemId: listID // 'C911CA15312F581D!530' = item.id를 입력
      }).then((childrens) => {
        // .daialab 아닌 파일은 disable
        var list = childrens.value;
        for(let i = 0; i < list.length; i++){
          // lastModifiedDateTime의 길이를 10글자로 잘라 저장
          var lastTime = list[i].lastModifiedDateTime.substr( 0, 10 );
          list[i].lastModifiedDateTime = lastTime;
          
          // .daia 여부를 확인하고 listData에 push
          var listName = list[i].name;
          var listNameLen = listName.length;
          var listLastDat = listName.lastIndexOf('.');
          // .뒤의 확장자 추출
          var fileLastDot = listName.substring(listLastDat,listNameLen).toLowerCase();
          // 확장자가 .daialab을 확인후 처리
          if(fileLastDot === '.daialab'){
            list[i].icon = 'layers';
            list[i].color = 'primary';
            list[i].startbtn = true;
            this.listData.push(list[i]);
          }else{
            list[i].icon = 'insert_drive_file';
            list[i].color = 'grey';
            this.listDatafile.push(list[i]);
          }
        }
        // returns body of https://dev.onedrive.com/items/update.htm#response
      })
    },
    CheckData(value){
      this.StartBtnReset();
      // dialog 활성화
      this.dialog = true;
      // this.fileName value.name 전송
      this.fileName = value.name;
      // download받을 id값 추출 
      var fileID = value.id;
      this.Download(fileID);
      // startbtn 활성화
      value.startbtn = false;
    },
    Download(fileID){
      var JSONStream = require('JSONStream');
      // JSONStream으로 대용량 JSON파일 parse하는 방법 
      // JSONStream.parse('*') value별로
      // JSONStream.parse('$*') value별로 받기
      var parser =  JSONStream.parse();
      // Stream 데이터 받기
      parser.on('data', (this.DownloadFile));

      var fileStream = oneDriveAPI.items.download({
        accessToken: this.$store.state.accessToken,
        itemId: fileID
        // filename: '파일 이름string' 으로 입력가능
      });

      // parser로 연결
      fileStream.pipe(parser);
    },
    DownloadFile(data){
      console.log("data",data);
      this.downloadData = data;
      //data를 vuex store로 저장
      this.$store.commit('setData', this.downloadData);
      //선행 data값 reset
      this.dialog = false;
    },
    Unsubscribe(){
      this.$socket.emit(this.$socketAPI.Unsubscribe);
    }
  },
  watch: {
    picked(value){
      // list reset
      this.listDatafile = [];
      this.listData = [];
      // 선택된 objcet List를 가져옴
      var pick = this.getData[value];
      // folder icon reset
      this.IconFolder();
      // openfolder 표시
      pick.icon = 'folder_open';
      // 선택된 id값을 GetList로 전송
      var listID = pick.id;
      this.GetList(listID);
    }
  },
  updated() {
    this.$nextTick(function () {
      try{
        if(this.$store.state.accessToken === null){
          this.$router.push('/'); //accessToken이 없다면 login화면으로 보내기
        }
      }catch(err){
        throw(err);
      }
    });
  }
}
</script>

