<template>
  <v-container fluid fill-height ma-0 pa-0 class="contact_bg">
    <v-layout column v-if="!isLogin">
      <v-flex class="loginBg">
        <v-layout column fill-height align-center justify-center class="loginLogo fadeIn animated delay-1s">
          <v-flex xs2>
            <img src="@/assets/pptplus_logo.svg" alt="PPT Plus Logo" class="pptPlusLogo">
          </v-flex>
          <v-flex xs2>
            <v-btn class="loginBtn" color="primary" outline v-on:click.native="GetAssessToken">START</v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex pa-3>
        <v-layout row wrap fill-height>
          <v-flex sm12 md6>
            <v-layout align-center justify-center fill-height class="mainContentLeft">
              <v-flex>
                <h3 class="mainText">PPT Plus를 활용하면 간단한 조작을 통해 Office Power Point에서 Data를 연동 시킬 수 있어요.</h3>
                <v-btn class="loginBtn mt-5" color="primary" outline>Dome</v-btn>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex sm12 md6>
            <img src="@/assets/ppt_plus.png" width="90%" class="bounceIn animated delay-2s">
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex pa-3 class="mainPartBg">
        <v-layout row wrap fill-height>
          <v-flex sm12 md6>
            <img src="@/assets/ppt_plus_web.png" width="90%" class="bounceIn animated delay-3s">
          </v-flex>
          <v-flex sm12 md6>
            <v-layout align-center justify-center fill-height class="mainContentRight">
              <v-flex>
                <h3 class="mainText">PPT를 활용하여 만들어진 파일을 PPT Plus Web 사이트를 통해 웹 브라이저에서 Presentation이 가능해요.</h3>
                <v-btn class="loginBtn mt-5" color="primary" outline>Dome</v-btn>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
 </template>

<script>
import * as Promise from 'bluebird';
import {mapGetters} from 'vuex';

export default {
	data: ()=>({
  }),
  computed: {
    isLogin(){
      return this.$store.getters.isLogin;
    }
  },
  created(){
    this.$eventBus.$on('Login', () =>{ this.GetAssessToken() });
    this.$eventBus.$on('Logout', () =>{ this.logout() });
  },
	methods: {
    GetAssessToken(){
      this.$store.state.myMSALObj.loginPopup(this.$store.state.requestObj).then(this.LoginSuccess).catch(function (error) {
        // Please check the console for errors
        console.log(error);
      });
    },
    LoginSuccess(loginResponse){
      // console.log("login success", loginResponse);
      this.acquireTokenPopupAndCallMSGraph();
    },
    acquireTokenPopupAndCallMSGraph() {
      //Call acquireTokenSilent (iframe) to obtain a token for Microsoft Graph
      this.$store.state.myMSALObj.acquireTokenSilent(this.$store.state.requestObj).then(this.preCallMSGraph)
      .catch( function (error) {
        console.log(error);
        // Call acquireTokenPopup (popup window) in case of acquireTokenSilent failure due to consent or interaction required ONLY
        if (error.indexOf("consent_required") !== -1 || error.indexOf("interaction_required") !== -1 || error.indexOf("login_required") !== -1) {
          this.$store.state.myMSALObj.acquireTokenPopup(this.$store.state.requestObj).then(this.preCallMSGraph)
          .catch( function (err) {
            console.log(err);
          });
        }
      })
    },
    preCallMSGraph(tokenResponse){
      // console.log("tokenResponse", tokenResponse);
      this.callMSGraph(this.$store.state.graphConfig.graphMeEndpoint, tokenResponse.accessToken, this.graphAPICallback);
    },
    graphAPICallback(data) {
      // console.log("data",data);
    },
    callMSGraph(theUrl, accessToken, callback) {
      // console.log("callMSGraph", theUrl, accessToken, callback)
      this.$store.commit('setToken', accessToken);
      var xmlHttp = new XMLHttpRequest();     
      xmlHttp.onreadystatechange = function () {
        if ( this.readyState === 4 && this.status === 200 ){
          callback(JSON.parse(this.responseText));
        }
      }
      xmlHttp.open("GET", theUrl, true); // true for asynchronous
      xmlHttp.setRequestHeader('Authorization', 'Bearer ' + accessToken);
      xmlHttp.send();
      this.$router.push('/List').catch(err => err);
    },
    logout() {
      this.$store.state.myMSALObj.logout();
    }
  },
  updated() {
    this.$nextTick(function () {
      if(this.$store.getters.isLogin === true){
        this.$router.push('/List').catch(err => err);
      }else{
        this.$router.push('/Home').catch(err => err);
      }
    });
  }
}
</script>

